<script>
  import katex from "katex";
  import Dynamic from "./Dynamic.svelte";
  import Calculated from "./Calculated.svelte";

  let VO2, BSA, MAP, feet, CM, KG, feetRemainder, CO, SV;
  let LB = 140,
    inches = 70,
    HGB = 14,
    Age = 50,
    SaO2 = 100,
    SvO2 = 70,
    CVP = 6,
    PASP = 18,
    PADP = 10,
    HR = 80,
    SBP = 120,
    DBP = 80,
    EF = 55,
    PAWP = 10;
  let HgbT = "Hgb";

  $: feet = inches / 12; // Converts inches to feet
  $: CM = inches * 2.54; // Converts inches to cm
  $: KG = LB * 0.45359237; // Converts lbs to kg
  $: feetRemainder = inches % 12; // for height, remainder inches

  $: BSA = Math.sqrt((inches * 2.54 * LB * 0.45359237) / 3600); // BSA based on height/weight
  $: Age >= 70 ? (VO2 = 110 * BSA) : (VO2 = 125 * BSA); // O2 delivery based on age and BSA
  $: CO = VO2 / (((SaO2 - SvO2) / 100) * HGB * 13.4); // Cardiac Output
  $: CO2 = (HR * SV) / 1000; // Cardiac output from SV
  $: CPO = (MAP * CO) / 451; // Cardiac Power Output
  $: CI = CO / BSA; // Cardiac Index

  $: SVR = (80 * (MAP - CVP)) / CO;
  $: SVRI = (80 * (MAP - CVP)) / CI;
  $: PVR = (80 * (MPAP - PAWP)) / CO;
  $: PVRI = (80 * (MPAP - PAWP)) / CI;

  $: MAP = (SBP + 2 * DBP) / 3;
  $: MPAP = (PASP + 2 * PADP) / 3;
  $: PAPI = (PASP - PADP) / CVP;

  $: SV = (CO / HR) * 1000;
  $: SVI = (CI / HR) * 1000;
  $: LVSW = SV * (MAP - PAWP) * 0.0136;
  $: LVSWI = SVI * (MAP - PAWP) * 0.0136;
  $: RVSW = SV * (MPAP - CVP) * 0.0136;
  $: RVSWI = SVI * (MPAP - CVP) * 0.0136;

  let equation = "CO = \\frac{HR\\times SV}{1000}\\, (4 - 8 L/min)";

  $: katexString = katex.renderToString(equation, {
    displayMode: true,
    throwOnError: false,
    output: "mathml",
  });
</script>

<main>
  <div class="container flex justify-center align-middle">
    <div
      class="flex flex-col w-full px-2 py-4 my-6 rounded-lg lg:p-24 lg:border-2 lg:border-solid lg:border-zinc-900"
    >
      <div class="py-4 border-b md:mb-12 top border-zinc-400">
        <h2>Extended Cardiac Physiology</h2>
        <div
          class="w-full px-4 py-2 my-2 overflow-x-auto rounded-md bg-zinc-100"
        >
          {@html katexString}
        </div>
        <div class="grid grid-cols-1 xl:grid-cols-3 lg:grid-cols-2 gap-x-10">
          <Calculated
            data={CO}
            min={4}
            max={8}
            on:eq={() =>
              (equation =
                "CO = \\frac{O_2\\,Delivery}{(S_aO_2 - S_vO_2)\\times 0.01\\times Hgb\\times 13.4}\\,(4 - 8 L/min)")}
          >
            Cardiac output (CO):
          </Calculated>

          <Calculated
            data={CI}
            min={2}
            max={4}
            on:eq={() => {
              equation = "CI = \\frac{CO}{BSA}\\,(2 - 4 L/min/m^2)";
            }}
          >
            Cardiac index (CI):
          </Calculated>

          <Calculated
            data={CPO}
            on:eq={() =>
              (equation =
                "CPO=\\frac{Mean\\,artery\\,pressure\\times Cardiac\\,output}{451}")}
            >Cardiac power output (CPO):</Calculated
          >

          <Calculated data={PAPI}>
            Pulmonary artery pulsatile index (PAPI):
          </Calculated>

          <Calculated
            data={SV}
            min={60}
            max={100}
            on:eq={() => (equation = "SV=\\frac{CO\\times HR}{1000}")}
          >
            >Stroke volume (SV):</Calculated
          >

          <Calculated data={SVI} min={33} max={47}
            >Stroke volume index (SVI):</Calculated
          >

          <Calculated data={MAP} min={70} max={105} eq={"(SBP + 2 * DBP) / 3"}
            >Mean artery pressure (MAP) (70-105 mmHg):</Calculated
          >

          <Calculated data={SVR} min={900} max={1440}>
            Systemic vascular resistance (SVR):
          </Calculated>

          <Calculated
            data={SVRI}
            min={1970}
            max={2390}
            eq={"80 * (MAP - CVP) / CI | 1970 – 2390 dynes · sec/cm5/m2"}
          >
            Systemic vascular resistance index (SVRI):
          </Calculated>

          <Calculated data={MPAP} min={10} max={20}>
            Mean pulmonary artery pressure (MPAP):
          </Calculated>

          <Calculated data={PVR} max={250}>
            Pulmonary vascular resistance (PVR):
          </Calculated>

          <Calculated data={PVRI} min={255} max={285}>
            Pulmonary vascular resistance index (PVRI):
          </Calculated>

          <Calculated data={LVSW} min={58} max={104}>
            Left ventricular stroke work (LVSW):
          </Calculated>

          <Calculated data={LVSWI} min={50} max={62}>
            Left ventricular stroke work index (LVSWI):
          </Calculated>

          <Calculated data={RVSW} min={8} max={16}>
            Right ventricular stroke work (RVSW):
          </Calculated>

          <Calculated data={RVSWI} min={5} max={10}>
            Right ventricular stroke work index (RVSWI):
          </Calculated>
        </div>
      </div>
      <div
        class="grid grid-cols-1 mt-4 md:mt-4 md:grid-cols-2 xl:grid-cols-3 gap-x-6 gap-y-2 bottom"
      >
        <Dynamic min={40} max={300} bind:result={LB}>
          Weight (lbs)
          {Math.round(KG)} Kg
        </Dynamic>

        <Dynamic min={53} max={76} bind:result={inches}>
          Height (in)
          {Math.floor(feet)}"{Math.round(feetRemainder)}
          {Math.round(CM * 100) / 100} cm
        </Dynamic>

        <Dynamic min={60} max={100} bind:result={SaO2}>SaO2</Dynamic>

        <Dynamic min={30} max={100} bind:result={SvO2}>SvO2</Dynamic>

        <Dynamic
          min={4}
          max={17}
          step={0.1}
          bind:result={HGB}
          on:slide={() => {
            HgbT;
          }}
        >
          Hemoglobin
        </Dynamic>

        <Dynamic min={1} max={100} bind:result={Age}>Age</Dynamic>

        <Dynamic min={20} max={200} bind:result={HR}>HR</Dynamic>

        <Dynamic min={1} max={15} bind:result={CO}>
          CO calculated using O2 delivery
        </Dynamic>

        <Dynamic min={1} max={15} bind:result={CO2}>
          CO calculated using stroke volume
        </Dynamic>

        <Dynamic min={10} max={150} bind:result={SV}>Stroke volume</Dynamic>

        <Dynamic min={0} max={100} bind:result={SVI}>
          Stroke volume index
        </Dynamic>

        <Dynamic min={60} max={240} bind:result={SBP}>
          Systolic blood pressure
        </Dynamic>

        <Dynamic min={10} max={120} bind:result={DBP}>
          Diastolic blood pressure
        </Dynamic>

        <Dynamic min={15} max={25} bind:result={PASP}>
          PA systolic pressure
        </Dynamic>

        <Dynamic min={8} max={15} bind:result={PADP}>
          PA diastolic pressure
        </Dynamic>

        <Dynamic min={0} max={20} bind:result={CVP}>CVP</Dynamic>

        <Dynamic min={0} max={35} bind:result={PAWP}>
          Pulmonary arterial wedge pressure
        </Dynamic>

        <Dynamic min={15} max={70} bind:result={EF}>Ejection fraction</Dynamic>
      </div>
    </div>
  </div>
</main>

